import React, { useEffect, useState } from "react";
import ChannelsList from "../../data/channels_list.json";
import Image from "next/image";
import "./channelChat.css";
import { IoArrowBackOutline } from "react-icons/io5";
import { AiOutlineUsergroupAdd } from "react-icons/ai";
import { loggedUser } from "../../Atoms/logged";
import { useRecoilValue } from "recoil";
import { userToken } from "@/app/Atoms/userToken";

interface channelChatProps {
  setSelectedChannel: React.Dispatch<React.SetStateAction<number>>;
  setShowPopUpCreateChannel: React.Dispatch<React.SetStateAction<boolean>>;
}

interface channelInterface {
  id: number;
  name: string;
  topic: string;
  type: string;
  uri: string;
}

const ChannelChat: React.FC<channelChatProps> = ({
  setSelectedChannel,
  setShowPopUpCreateChannel,
}) => {
  const userId = useRecoilValue(loggedUser);
  const userTok = useRecoilValue(userToken);
  const [channels, setChannels] = useState<channelInterface[]>();

  useEffect(() => {
    const fetchChannels = async () => {
      try {
        const response = await fetch(
          `http://localhost:3000/channelss/${userId}`,
          {
            headers: {
              Authorization: `Bearer ${userTok}`,
              "Content-Type": "application/json",
            },
          }
        );
        if (!response) {
          console.log("Error");
        }
        const data = await response.json();
        const channelsArr = data.map((data: any) => data.channels);
        setChannels(channelsArr);
        console.log("---CHANNELSS---->", channelsArr);
      } catch (error) {
        console.log("Error111");
      }
    };
    fetchChannels();
  }, []);

  return (
    <>
      <div className="channelsListContainer">
        {channels?.map((channel) => {
          return (
            <div
              key={channel.id}
              className="channelContainer"
              onClick={() => {
                setSelectedChannel(channel.id);
              }}
            >
              <div className="imageContainer">
                <Image
                  className="channelImage"
                  src={channel.uri}
                  width={80}
                  height={80}
                  alt="avatar"
                />
              </div>
              <div className="name_lastmsg">
                <p>
                  {channel.name.length > 10
                    ? `${channel.name.substring(0, 10)}..`
                    : channel.name}
                </p>
                {/* <span>
                  {e.lastmsg.length > 10
                    ? `${e.lastmsg.substring(0, 10)}..`
                    : e.lastmsg}
                </span> */}
              </div>
            </div>
          );
        })}
        {/* {ChannelsList.map((e) => {
          return (
            
          ); */}
        {/* })} */}
      </div>
      <div
        className="addChannelBtn"
        onClick={() => setShowPopUpCreateChannel(true)}
      >
        <AiOutlineUsergroupAdd />
      </div>
    </>
  );
};

export default ChannelChat;
